/*collections*/

.collection-container
{
	width: 100%;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 10px;

}

.collection
{
	position: relative;
}

.collection img
{
	width: 100%;
	height:100%;
	object-fit: cover;
}

.collection p
{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%, -50%);
	text-align: center;
	color: #fff;
	font-size: 50px;
	text-transform: capitalize;
}

.collection:nth-child(3)
{
	grid-column: span 2;
	margin-bottom: 10px;
}